<template>
  <yd-layout class="appCenter" :class="{'allEdit' : editFlag, 'more' : myApp.length > 4}">
    <yd-navbar title="应用中心" bgcolor="#59a5fd" color="#fff" border-color="#59a5fd">
      <router-link to="/" slot="left">
        <yd-navbar-back-icon color="#fff">返回</yd-navbar-back-icon>
      </router-link>
    </yd-navbar>
    <div class="homeBox" v-if="searchFlag">
      <div class="search active">
        <input class="searchWord" type="text" v-model="applicationName" placeholder="热门搜索来点我" />
        <span @click="searchFlag = false;applicationName='';searchArr=[];" class="cancel">取消</span>
        <div @click="applicationName='';" class="close">
          <img src="../../../static/images/delete@3x.png" alt="">
                </div>
        </div>
      </div>
      <div class="searchPage" v-show="searchFlag">
        <dl v-if="applicationName==''" class="searchRec">
          <dt>搜索推荐</dt>
          <dd v-for="(item,index) in myApp" v-if="index<4" :key="index">
            <yd-flexbox>
              <img :src="'../../../static/images/workCenter/'+item.img">
              <p>
                <span class="tit" v-text='item.name'></span>
              </p>
            </yd-flexbox>
          </dd>
        </dl>
        <div class="noRes" v-if="searchArr.length < 1&&applicationName!=''">
          <img src="../../../static/images/workCenter/noRestult3.png">
          <p>暂时没有匹配的搜索结果哦</p>
        </div>
      </div>
      <template v-if="!searchFlag">
        <div @touchmove.prevent class="myApp firBox" :class="myApp.length > 4 ? 'more' : ''" v-show="editFlag">
          <div class="colTop nobor">
            <div class="colLeftTxt">我的应用</div>
            <div class="colRightTxt editBox" @click="complete()">完成</div>
          </div>
          <div class="appbox" :class="myApp.length > 4 ? 'more' : ''" ref="move_app">
            <div class="move_item" @touchstart="longPress($event)" v-for="(item, index) in myApp" :key="index">
              <div slot="icon" class="imgBox">
                <img :src="'../../../static/images/workCenter/'+item.img">
                <div @touchstart.stop @click.stop="deleteApp(item.name)" class="delete" :class="editFlag ? 'edit' : 'common'">
                  <img src="../../../static/images/app/appDelete.png" alt="">
                            </div>
                </div>
                <span class="text" v-text="item.name"></span>
              </div>
            </div>
          </div>
          <div class="myApp topbor" v-show="!editFlag">
            <div class="colTop ">
              <div class="colLeftTxt">我的应用
              </div>
              <div class="myimg">
                <img v-for="(item, index) in myApp" :key="index" :src="'../../../static/images/workCenter/'+item.img" alt="">
                    </div>
                <div class="colRightTxt searchBox" @click="searchFlag=true">
                  <img src="../../../static/images/app/appSearch.png" alt="">
                    </div>
                  <div class="colRightTxt editBox" @click="editFlag=true">编辑</div>
                </div>
              </div>
              <div class="myApp infoRelease" v-if="!editFlag">
                <div class="colTop">
                  <div class="colLeftTxt">重磅更新</div>
                  <div class="colRightTxt">查看更多</div>
                </div>
                <yd-grids-group :rows="4" class="newApps">
                  <yd-grids-item @click.native="accessRecord('应用中心-' + item.CHNLNAME);jumpOut(item)" v-for="(item, index) in updataApp.slice(0, 4)" :key="index">
                    <div slot="icon" class="imgBox newImgBox">
                      <img :src="'../../../static/images/workCenter/'+item.img" />
                      <div class="new edit">
                        <img class="" :class="{'tada' : animateFlag == true}" src="../../../static/images/app/appNew.png" alt="">
                            </div>
                      </div>
                      <span slot="text" v-text="item.name"></span>
                  </yd-grids-item>
                  <yd-grids-item v-for="n in fillNum" :key="n + 'bbbbbbbbb'">
                    <div slot="icon" class="imgBox newImgBox">
                      <img :src="'../../../static/images/app/qidai.png'" />
                        </div>
                      <span slot="text" v-text="'敬请期待'"></span>
                  </yd-grids-item>
                </yd-grids-group>
              </div>
              <div v-if="loadFlag" class="contB">
                <div class="myApp infoRelease">
                  <div class="colTop">
                    <div class="colLeftTxt">信息发布</div>
                  </div>
                  <yd-grids-group class="appHeight" :class="allOpen[0] ? 'all' : ''" :rows="4">
                    <yd-grids-item v-for="(item, index) in infoApp" :key="index">
                      <div slot="icon" class="imgBox">
                        <img :src="'../../../static/images/workCenter/'+item.img" />
                        <div v-if="item.type == 'delete'" @click="deleteApp(item.CHNLNAME)" class="delete" :class="editFlag ? 'edit' : 'common'">
                          <img src="../../../static/images/app/appDelete.png" alt="">
                                </div>
                          <div v-if="item.type == 'add'" @click="addApp(item)" class="delete" :class="editFlag ? 'edit' : 'common'">
                            <img src="../../../static/images/app/appAdd.png" alt="">
                                </div>
                          </div>
                          <span slot="text" v-text="item.CHNLNAME"></span>
                    </yd-grids-item>
                  </yd-grids-group>
                </div>
                <div class="myApp infoRelease">
                  <div class="colTop">
                    <div class="colLeftTxt">人事服务</div>
                  </div>
                  <yd-grids-group class="appHeight" :class="allOpen[0] ? 'all' : ''" :rows="4">
                    <yd-grids-item v-for="(item, index) in perApp" :key="index">
                      <div slot="icon" class="imgBox">
                        <img :src="'../../../static/images/workCenter/'+item.img" />
                        <div v-if="item.type == 'delete'" @click="deleteApp(item.CHNLNAME)" class="delete" :class="editFlag ? 'edit' : 'common'">
                          <img src="../../../static/images/app/appDelete.png" alt="">
                                </div>
                          <div v-if="item.type == 'add'" @click="addApp(item)" class="delete" :class="editFlag ? 'edit' : 'common'">
                            <img src="../../../static/images/app/appAdd.png" alt="">
                                </div>
                          </div>
                          <span slot="text" v-text="item.CHNLNAME"></span>
                    </yd-grids-item>
                  </yd-grids-group>
                </div>
              </div>
      </template>
  </yd-layout>
</template>
<script>
import { mapGetters, mapMutations } from 'vuex';
export default {
  name: 'appCenter',
  data() {
    return {
      searchArr: [],
      isHost: false,
      searchHost: [],
      searchHostKey: [],
      searchFlag: false,
      applicationName: '',
      editFlag: false,
      myApp: [],
      changeApp: [],
      loadFlag: true,
      allOpen: [false, false, false, false, false],
      aPos: [],
      animateFlag: true,
      Loop: '',
      touchFlag: false,
      animateTime: '',
      fillNum: 0,
      myOldApp: [],
      myApp2: [{
        img: 'a1.png',
        name: '公司公告'
      }, {
        img: 'a2.png',
        name: '请休假'
      }, {
        img: 'a3.png',
        name: '绩效管理'
      }, {
        img: 'a4.png',
        name: '未接来电'
      }, {
        img: 'a5.png',
        name: '智能开票'
      }, {
        img: 'a6.png',
        name: '数据经纬'
      }, {
        img: 'a7.png',
        name: '差率费用标准'
      }],
      infoApp: [{
        'CHNLNAME': '要闻公告',
        'img': 'a4.png'
      }, {
        'CHNLNAME': 'MCC查询',
        'ISRECOMM': 1,
        'img': 'a3.png'
      }, {
        'CHNLNAME': '境内技术服务',
        'img': 'a2.png'
      }, {
        'CHNLNAME': '十九大学习',
        'img': 'a2.png'
      }, {
        'CHNLNAME': '学分查询',
        'img': 'a1.png'
      }],
      perApp: [{
        'CHNLNAME': '公司公告',
        'img': 'a3.png'
      }, {
        'CHNLNAME': '绩效管理',
        'img': 'a2.png'
      }, {
        'CHNLNAME': '未接来电',
        'img': 'a2.png'
      }, {
        'CHNLNAME': '智能开票',
        'img': 'a1.png'
      }, {
        'CHNLNAME': '数据经纬',
        'img': 'a1.png'
      }, {
        'CHNLNAME': '差旅费用标准',
        'img': 'a1.png'
      }],
      updataApp: [{
        img: 'a1.png',
        name: '公司公告'
      }, {
        img: 'a2.png',
        name: '请休假'
      }, {
        img: 'a3.png',
        name: '绩效管理'
      }, {
        img: 'a4.png',
        name: '未接来电'
      }]
    }
  },
  mounted() {
    let vm = this;
    if (vm.getAppCenter.myApp.length === 0) {
      vm.myApp = vm.myApp2
      vm.setAppCenter({
        myApp: vm.myApp
      });
    } else {
      vm.myApp = vm.getAppCenter.myApp;
    }
    vm.chooseType(vm.infoApp);
    vm.chooseType(vm.perApp);
  },
  computed: {
    ...mapGetters(['getAppCenter'])
  },
  methods: {
    ...mapMutations([
      'setAppCenter'
    ]),
    addApp(item) {
      let vm = this;
      if (vm.myApp.length >= 7) {
        vm.$dialog.toast({
          mes: '最多只能选择7个应用',
          timeout: 1500
        })
      } else {
        let obj = {};
        obj.name = item.CHNLNAME;
        obj.img = item.img;
        vm.myApp.push(obj);
        vm.chooseType(vm.infoApp);
        vm.chooseType(vm.perApp);
      }
    },
    deleteApp(name) {
      const vm = this;
      document.body.removeEventListener('touchmove', vm.scrollerFun, false);
      if (vm.myApp.length <= 1) {
        this.$dialog.toast({
          mes: '请至少保留一个应用',
          timeout: 1500,
          callback: () => {
            return false;
          }
        })
      } else {
        this.backPos();
        vm.myApp = vm.myApp.filter((res) => {
          return res.name != name;
        });
        this.chooseType(vm.infoApp);
      }
    },
    backPos() {
      const vm = this;
      vm.myOldApp = vm.myApp.slice(0);
      if (vm.touchFlag) {
        let aLi = document.querySelectorAll('.move_item');
        // myAPP的位置同样替换掉
        vm.changeApp = new Array(aLi.length);
        aLi.forEach((item, num) => {
          vm.changeApp[item.index] = vm.myOldApp[num];
        });
        vm.myApp = vm.changeApp;
        aLi.forEach((item, num) => {
          item.style.left = 0;
          item.style.top = 0;
          item.style.position = 'static';
        });
        vm.touchFlag = false;
      }
    },
    complete() {
      const vm = this;
      vm.editFlag = false;
      vm.setAppCenter({
        myApp: vm.myApp
      });
    },
    chooseType(arrApp) {
      const vm = this;
      arrApp.map(function(data) {
        vm.myApp.some(function(val) {
          return val.name == data.CHNLNAME;
        }) ? data.type = 'delete' : data.type = 'add';
      });
    }
  }
}

</script>
<style lang="less">
@import "./appCenter.less";

.yd-scrollview:after {
  height: 0;
}

.allEdit .yd-scrollview {
  // padding-top: 2.9rem;
  // overflow: hidden;
}

.allEdit.more .yd-scrollview {
  // padding-top: 5rem;
}

.allEdit .contB {
  overflow: auto;
  // height: 100%; // padding-bottom: 3rem;
}

.allEdit.more .contB {
  // padding-bottom: 5rem;
}

.allEdit>* {
  -webkit-touch-callout: none;
  /*系统默认菜单被禁用*/
  -webkit-user-select: none;
  /*webkit浏览器*/
  -khtml-user-select: none;
  /*早期浏览器*/
  -moz-user-select: none;
  /*火狐*/
  -ms-user-select: none;
  /*IE10*/
  user-select: none;
}

.setTop {
  margin-top: 0;
  height: 1.1rem;
  background-color: #fff;

  .yd-flexbox {
    line-height: 1.1rem;
    font-size: 0.34rem;
    color: #999;

    .yd-flexbox-item {
      text-align: center;
      position: relative;
    }

    .active {
      color: #333;

      span {
        display: block;
        height: 0.06rem;
        width: 1.64rem;
        border-radius: 2px;
        background-color: #59a5fd;
        position: absolute;
        bottom: -0.03rem;
        left: 0.42rem;
      }
    }
  }
}

.yd-grids-icon img {
  margin: 0 !important;
  height: .8rem;
  width: .8rem;
}

.imgBox {
  width: 1.18rem;
  height: .8rem;
  position: relative;
  text-align: center;

  .common {
    display: none;
  }

  .edit {
    display: block;
    width: 18px;
    height: 18px;
    position: absolute;
    top: -0.06rem;
    right: -0.04rem;

    img {
      width: 100%;
      height: 100%;
    }
  }

  .new {
    width: 0.44rem;
    height: 0.38rem;
    top: -0.12rem;
    right: -0.14rem;
  }
}

.newImgBox {
  width: 1.34rem;
  height: 1rem;
  border: 0.08rem solid #fff;
  border-radius: 50%;
}

.appbox {
  padding-bottom: 10px;
  position: relative;
  padding-top: 13px;
  height: 2rem;

  &.more {
    height: 3.4rem;
  }

  .move_item {
    width: 25%;
    float: left;
    text-align: center;
    position: relative;
    padding-top: 0.2rem;
  }

  .imgBox {
    margin: 0 auto;

    img {
      width: .8rem;
      height: .8rem;
    }

    .edit {
      img {
        width: 18px;
        height: 18px;
      }
    }
  }

  .text {
    margin-top: 0.13rem;
    display: inline-block;
  }
}

.colTop {
  .myimg {
    padding-left: 0.1rem;
    float: left;
    padding-top: 0.03rem;

    img {
      width: 0.4rem;
      height: 0.4rem;
      margin-left: 0.13rem;
    }
  }
}

.firBoxO {
  // position: fixed;
  // top: 0;
  z-index: 99;
  width: 100%;
}

.myApp {
  padding-bottom: 12px;
  padding-top: 12px; // overflow: hidden;
  position: relative;

  &.firBox {
    height: 2.9rem;
    // position: fixed;
    width: 100%;
    // top: 0;
    z-index: 99;
    -webkit-overflow-scrolling: touch;
  }

  &.firBox::after {
    content: "";
    height: 0.2rem;
    width: 100%;
    position: absolute;
    bottom: -0.18rem;
    background: #f8f8f8;
    left: 0;
  }

  &.firBox.more {
    height: 4.2rem;
  }
}

.moreApp {
  width: 0.3rem;
  height: 0.24rem;

  img {
    width: 100%;
    height: 100%;
  }
}

.editBox {
  width: 0.92rem;
  height: 0.52rem;
  line-height: 0.52rem;
  border: 1px solid #59a5fd;
  text-align: center;
  margin-right: 0.28rem;
  border-radius: 3px;
}

.searchBox {
  width: 0.4rem;
  height: 0.4rem;
  padding: 0;
  margin: 0;
  margin-right: 0.28rem;
  margin-top: 0.04rem;

  img {
    width: 100%;
  }
}

.appHeight {
  height: auto;
}

.appHeight.all {
  max-height: max-content;
}

.addclass .yd-scrollview {
  // overflow: hidden;
}

@keyframes shake {
  0% {
    transform: rotate(-4deg);
  }

  to {
    transform: rotate(4deg);
  }
}

.shake .imgBox {
  animation-name: shake;
  animation-duration: 0.07s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

.yd-scrollview {
  margin-top: 0 !important;
}

// 粘性定位
.ios .firBox.myApp.sticky {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 999;
}

</style>
<style lang="less" scoped>
.homeBox {
  z-index: 9999;
  position: relative;
}

.ios .search.active {
  .searchWord {
    width: 88%;
  }

  .close {
    top: .5rem;
  }

  .cancel {
    top: .5rem;
  }
}

.search {
  background: #4ec4ff;
  padding: .3rem;
  padding-top: .5rem;
  position: fixed;
  overflow: hidden;
  height: 1.5rem;
  width: 100%;
  top: 0;
  z-index: 999;
  outline: none !important;
  border: none !important;

  &.active {
    .searchWord {
      width: 88%;
    }

    .close {
      top: .5rem;
    }

    .cancel {
      top: .5rem;
    }
  }

  .cancel {
    color: #fff;
    font-size: .3rem;
    position: absolute;
    right: .26rem;
    top: .3rem;
    height: .7rem;
    line-height: .7rem;
    text-align: center;
  }

  .close {
    width: .66rem;
    height: .7rem;
    text-align: center;
    position: absolute;
    top: .3rem;
    right: 1.2rem;
    line-height: .65rem;

    img {
      width: .38rem;
      vertical-align: middle;
    }
  }

  .searchWord {
    background-color: #0b9ee5;
    background-position: .27rem;
    border-radius: .08rem;
    text-align: left;
    padding-left: .66rem;
    padding-right: .6rem;
    color: #fff;
  }
}

.searchWord {
  width: 100%;
  height: .7rem;
  padding-top: .17rem;
  padding-bottom: .22rem;
  border: 0;
  box-sizing: border-box;
  text-align: center;
  outline: none;
  border-radius: 2px;
  margin-right: 10px;
  transition: border-color 0.2s ease;
  background: #fff url(../../../static/images/search@3x.png) 47% center no-repeat;
  background-size: .26rem;
  padding-left: .56rem;
  font-size: .28rem;
  vertical-align: middle !important;
}

:-moz-placeholder {
  /* Mozilla Firefox 4 to 18 */
  color: #fff !important; // font-size: 0.32rem !important;
}

::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: #fff !important; // font-size: 0.32rem !important;
}

input:-ms-input-placeholder {
  color: #fff !important; // font-size: 0.32rem !important;
}

input::-webkit-input-placeholder {
  color: #fff !important; // font-size: 0.32rem !important;
}

.ios .searchPage {
  margin-top: .5rem;
}

.searchPage {
  margin-top: .5rem;
  position: relative;
  z-index: 999;

  .noRes {
    text-align: center;
    padding-top: 2.5rem;

    img {
      width: 3.3rem;
      margin-bottom: .25rem;
    }

    p {
      font-size: .28rem;
    }
  }

  dl {
    background: #fff;
    margin-top: .16rem;

    dt {
      padding-left: .36rem;
      color: #333;
      font-size: .32rem;
      padding-top: .24rem;
      font-weight: 600;
    }

    dd {
      height: 1.8rem;
      line-height: 1.8rem;
      border-bottom: 1px solid #efefef;
      padding-left: .3rem;
      font-size: .32rem;

      img {
        width: 1.2rem;
        margin-right: .34rem;
      }
    }
  }
}

.search-button {
  padding: .24rem 0 0 .36rem;
  max-height: 1.62rem;
  overflow: hidden;

  .yd-btn-primary:not(.yd-btn-loading) {
    color: #666;
    background-color: #e9e9e9;
    font-size: .32rem;
    margin-right: .21rem;
    line-height: .6rem;
    margin-bottom: .1rem;
  }
}

.host {
  background: #fff;
  padding-top: .24rem;

  .tit {
    padding-left: .36rem;
    color: #333;
    font-size: .32rem;
    font-weight: 600
  }
}

.yd-grids-4 .yd-grids-item:not(:nth-child(4n)):before {
  background-image: none;
}

.yd-grids-item:after {
  background-image: none;
}

</style>



